import React, { Component } from 'react';
import './index.scss';

import classNames from 'classnames';
import _ from 'underscore';

import Button from '../button';

export default class Counter extends Component {

  static defaultProps = {
    value: 0, // default value
    min: 0,
    max: 999,
    gap: 1, //  1
  };

  constructor(props) {
    super(props);

    const { value } = this.props;
    const defaultValue = !_.isUndefined(value) && this.isValid(value)
      ? value
      : 0;

    this.state = {
      value: defaultValue,
      hover: false,
    };
  }

  isValid(value) {
    const { min, max } = this.props;
    if (!_.isNumber(value)) {
      return false;
    }

    return value >= min && value <= max;
  }

  handleValueChange = (type, rate = 1) => {
    const { value } = this.state;
    const { gap } = this.props;
    const newValue = type === 1
      ? value + gap * rate
      : value - gap * rate;

    if (this.isValid(newValue)) {
      this.setState({
        value: newValue
      })
    }
  };

  handlePlusClick = () => {
    this.handleValueChange(1);
  };

  handleMinusClick = () => {
    this.handleValueChange(2);
  };

  handlePlusTenClick = () => {
    this.handleValueChange(1, 10);
  };

  handleMinusTenClick = () => {
    this.handleValueChange(2, 10);
  };

  handleMouseEnter = () => {
    this.setState({hover: true});
  };

  handleMouseLeave = () => {
    this.setState({hover: false});
  };

  render() {
    const { value, hover } = this.state;
    const counterClazz = classNames({
      'oli-counter': true,
    });

    return (
      <div
        className={counterClazz}
        onMouseEnter={this.handleMouseEnter}
        onMouseLeave={this.handleMouseLeave}
      >
        {
          hover && (
            <div className="top fadeInDown animated">
              <Button
                icon="plus"
                size="xs"
                onClick={this.handlePlusClick}
              />
              <Button
                icon="plus"
                size="xs"
                onClick={this.handlePlusTenClick}
              >10</Button>
            </div>
          )
        }

        <div className="value-control">
          <span>{value}</span>
        </div>

        {
          hover && (
            <div className="bottom fadeInUp animated">
              <Button
                icon="minus"
                size="xs"
                onClick={this.handleMinusClick}
              />
              <Button
                icon="minus"
                size="xs"
                onClick={this.handleMinusTenClick}
              >10</Button>
            </div>
          )
        }
      </div>
    );
  }
}
